.onboarding1 {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 20px;
  background-color: var(--color-white);
  width: 375px;
  height: 812px;
}
.home-indicator1 {
  position: absolute;
  height: calc(100% - 26px);
  width: calc(100% - 241px);
  top: 18px;
  right: 120px;
  bottom: 8px;
  left: 121px;
  border-radius: 100px;
  background-color: #1f2937;
}
.home-indicator {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 375px;
  height: 31px;
}
.cap-icon {
  position: absolute;
  top: calc(50% - 2px);
  right: 0;
  width: 1.33px;
  height: 4px;
  opacity: 0.4;
}
.capacity {
  position: absolute;
  height: calc(100% - 4px);
  top: 2px;
  bottom: 2px;
  left: 2px;
  border-radius: 1.33px;
  background-color: var(--color-gray-100);
  width: 18px;
}
.battery,
.wifi-icon {
  position: absolute;
  top: calc(50% - 4.67px);
}
.battery {
  right: 14.67px;
  border-radius: 2.67px;
  border: 1px solid var(--color-gray-100);
  box-sizing: border-box;
  width: 24.33px;
  height: 11.33px;
}
.wifi-icon {
  right: 44.03px;
  width: 15.27px;
  height: 10.97px;
}
.cellular-connection-icon {
  position: absolute;
  top: calc(50% - 4.33px);
  left: calc(50% + 106.17px);
  width: 17px;
  height: 10.67px;
}
.time {
  position: absolute;
  width: calc(100% + 1px);
  top: calc(50% - 8px);
  left: 0;
  letter-spacing: 0.3px;
  font-weight: 500;
  display: inline-block;
}
.time-style {
  position: absolute;
  top: calc(50% - 6.06px);
  left: 20px;
  width: 54px;
  height: 16px;
}
.iphone-xstatus-barsstatus-ba {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.top-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 375px;
  height: 44px;
}
.skip {
  width: calc(100% + 1px);
  top: calc(50% - 9.5px);
  left: 0;
  letter-spacing: 0.16px;
  line-height: 20px;
  display: inline-block;
}
.button1,
.logo,
.skip {
  position: absolute;
}
.logo {
  top: 61.54px;
  right: 24px;
  width: 30px;
  height: 19px;
  font-size: var(--font-size-base);
  color: var(--color-slategray);
}
.button1 {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 1000px;
  background-color: var(--color-royalblue);
}
.message-icon {
  position: absolute;
  top: calc(50% - 12px);
  left: 17.5px;
  width: 24px;
  height: 24px;
  display: none;
}
.label {
  position: absolute;
  top: calc(50% - 8.96px);
  left: calc(50% - 40.5px);
  font-size: var(--font-size-base);
  letter-spacing: 0.16px;
  line-height: 20px;
  font-weight: 500;
  font-family: var(--font-sf-pro-display);
  color: var(--color-white);
  text-align: center;
  display: inline-block;
  width: 82px;
}
.message-icon1 {
  position: absolute;
  top: calc(50% - 12px);
  left: calc(50% - 55px);
  width: 24px;
  height: 24px;
  display: none;
}
.button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  bottom: 56px;
  left: 24px;
  width: 327px;
  height: 48px;
}
.trusted-contractor {
  color: var(--color-royalblue);
}
.get-the-best-container,
.the-better-the {
  margin: 0;
  position: absolute;
  width: calc(100% + 1px);
  left: 0;
  display: inline-block;
}
.get-the-best-container {
  top: 0;
  font-size: inherit;
  line-height: 41px;
  font-weight: 700;
  font-family: inherit;
}
.the-better-the {
  bottom: 0;
  font-size: var(--font-size-base);
  letter-spacing: 0.16px;
  line-height: 20px;
  color: var(--color-slategray);
}
.tittle {
  position: absolute;
  top: 451.08px;
  left: 24px;
  width: 327px;
  height: 193.32px;
  text-align: left;
  font-size: 32px;
  color: var(--color-gray-200);
}
.indicator-icon {
  position: absolute;
  bottom: 140px;
  left: 174.5px;
  width: 26px;
  height: 8px;
}
.logo-icon,
.vuesaxboldgallery-icon {
  position: absolute;
  top: 137px;
  left: 0;
  width: 380px;
  height: 278px;
  object-fit: cover;
}
.logo-icon {
  top: 44px;
  left: 13px;
  width: 118px;
  height: 45px;
}
.onboarding {
  position: relative;
  background-color: transparent;
  width: 100%;
  height: 812px;
  text-align: center;
  font-size: 14px;
  color: var(--color-gray-100);
  font-family: var(--font-sf-pro-display);
}
